@import url("https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap");

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  background-color: black;
  color: white;
}

.container {
  height: 100vh;
  width: 100vw;
}

.center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.button {
  border-radius: 7px;
  position: relative;
  padding: 20px 50px;
  font-weight: 500;
  font-size: 1.5rem;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  border: 2px solid transparent;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s, border 0.3s;
}

.button:hover {
  background-color: #333333;
  background-size: 200%;
  color: black;
  border: 1px solid white;
  transform: translateY(-5px);
}

.button span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  transition: opacity 0.3s;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
}

.button:hover span {
  opacity: 0.2;
}

.shine-overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #333333;
  opacity: 0.3;
  transition: opacity 0.3s, transform 0.3s;
  z-index: -2;
}

.shine-overlay.active {
  opacity: 0.8;
  transform: scale(1.2);
}

.backgroundVideoWrapper {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.backgroundVideo {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  transform: translate(-50%, -50%);
  z-index: -1;
}

.center video::-webkit-media-controls {
  display: none !important;
}

.audio {
  display: none;
}
.button {
  border-radius: 7px;
  position: relative;
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  color: white;
  background-color: #333;
  border: none;
  cursor: pointer;
}

.button:hover span {
  opacity: 1;
}

.button span {
  border-radius: 6px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.3s;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
  animation: gradient 1s ease infinite;
  background-image: linear-gradient(to right, #333333, #dd1818 );
}

@keyframes gradient {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}